import { Component } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  
  codigoprom: string ='';
  correoElectronico: string ='';
  nombreUsuario: string ='';
  apellidoUsuario: string ='';
  empresa: string ='';
  primeraDireccion: string ='';
  segundaDireccion: string ='';
  ciudad: string ='';
  codigoPostal: number;
  telefono: number;
  province_selected: string;

  selectCountry:string;

  constructor(private router: Router) {}

  pasarDato(){
    let navigationExtras: NavigationExtras ={
      queryParams: {
        codigoprom: this.codigoprom,
        correoElectronico: this.correoElectronico,
        nombreUsuario: this.nombreUsuario ,
        apellidoUsuario: this.apellidoUsuario,
        empresa: this.empresa,
        primeraDireccion: this.primeraDireccion,
        segundaDireccion: this.segundaDireccion,
        ciudad: this.ciudad,
        codigoPostal: this.codigoPostal,
        telefono: this.telefono,
        province_selected: this.province_selected,
        selectCountry: this.selectCountry,
      }
    };
    this.router.navigate(['direction'], navigationExtras);
  }
   
}
